
body {
  font-family: 'Montserrat', sans-serif !important;
}

.no-border:focus {
  /* border: 0; */
  box-shadow: none; /* You may want to include this as bootstrap applies these styles too */
}

.menu {
    color: black;
    text-decoration: none;
    font-size: 100%;
    align-items: center;
    vertical-align: middle;
    text-shadow: 5px;
}

.menuVideo {
  font-size: 90%;
}

.menu:hover {
  color: rgb(212, 52, 52);
  transform: scale(1.1) !important;
  transition: transform .2s; /* Animation */
  cursor: pointer;

}

.menu2 {
  font-size: 100%;
}

.menu2:hover {
  font-size: 110%;
  transition: transform .2s; /* Animation */
}

#omeni {
  transform: scale(2) !important;
}

.fotke { 
    transition: transform .2s; /* Animation */
}

.fotke:hover { 
    transform: scale(2.5);
    transition: transform .4s; /* Animation */
  }
  
  .ikonice {
    opacity: 0.5;
  }
  
  .ikonice:hover {
    opacity: 1;
    transform: scale(1.2);
    transition: transform .4s; /* Animation */
}

.videos {
    display: inline
}

.images:hover {
    transition: 0.71s;
    transform: scale(1.05);
    /* border: 5px solid white;
    border-radius: 5px; */
}

.loading {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 100px;
}


.footer {
  color: gray;
  font-size: 10px;
  position: relative;

  left:0;
  bottom:0;
  right:0;  

}

body {
  min-height: 100%;
}

html {
  height: 100;
}

#jezik {
  text-decoration: none;
  font-size: 12px;
  color: red;
}

.galerija {
  display: flex;
  justify-content: center;
}

.kontakt {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  min-height: 100vh;
}

.profilna {
  width: 200px;
  border-radius: 50%;
}

.omeni {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.omeni-text {
  width: 50%;
}

.slike-saradnja {
  width: 100px;
}

.kontaktForma {
  width: 50%;
}


.gruzanska {
  width: 65px;
}

.kontaktIme {
  width: 50%;
}

@media (max-width: 600px) {
  .omeni-text {
    width: 80%;
  }
  .slike-saradnja {
    width: 50px;
  }
  .slike {
    width: 80%;
  }
  .kontaktForma {
    width: 100%;
  }
  .kontaktIme {
    width: 60%;
  }
}



